import { Axios } from 'axios'
import {
  Chain,
  ChainRoutes,
  ChainsClientModel,
  ChainsSearchQuery,
} from '../chain.model'
import { ApiEntity } from '../../core'

export class ChainsClient extends ApiEntity implements ChainsClientModel {
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Search chains by params
   * @param searchParams - the filters to apply
   * @returns a chains page
   */
  public search(searchParams?: ChainsSearchQuery) {
    return this._search<Chain>(ChainRoutes.v1Search, searchParams)
  }

  /**
   * List chains by params
   * @param searchParams - the filters to apply
   * @returns the chains list
   */
  public list(searchParams?: ChainsSearchQuery) {
    return this._list<Chain>(ChainRoutes.v1Search, searchParams)
  }

  /**
   * Find a chain by params
   * @param searchParams - the filters to apply
   * @returns an optional chain
   */
  public findOne(searchParams?: ChainsSearchQuery) {
    return this._findOne<Chain>(ChainRoutes.v1Search, searchParams)
  }

  /**
   * Read a chain by params or throw
   * @param searchParams - the filters to apply
   * @returns the matching chain
   */
  public readOne(searchParams: ChainsSearchQuery) {
    return this._readOne<Chain>(ChainRoutes.v1Search, searchParams)
  }
}
